/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

* {
  outline: none; }

body,html {
  @include responsive(1400px,769px) {
    font-size: 18px;
    font-weight: 100;
    line-height: 1.5;
    position: relative; } }
img {
  max-width: 100%; }

h3 {
  font-weight: 800;
  @include maxtablet {
    font-size: 26px;
    margin-bottom: 30px; } }
p {
  @extend .text-light;
  @include maxtablet {
    font-size: 13px;
    line-height: 1.5; } }

.container {
  padding: 0 25px;
  text-align: center; }

.section {
  min-height: 100vh;
  position: relative;
  z-index: 80;
  @include mobile {
    min-height: 50vh; } }

.owl-carousel {
  .owl-nav {
    @extend .clearfix;
    position: absolute;
    top: calc(50% - 30px);
    width: 100%;
    .owl-prev,
    .owl-next {
      padding: 8px 0;
      width: 25px;
      position: absolute;
      font-size: 14px;
      line-height: 20px;
      transition: all 0.3s; }
    .owl-next {
      right: 0;
      .fa {} }
    .owl-prev {
      left: 0;
      .fa {} } }
  .owl-dots {
    bottom: 30px;
    .owl-dot {
      opacity: .5;
      @include responsive(0, 1024px) {
        margin: 0 15px; }
      &.active {
        opacity: 1; }
      span {
        background: #d7d7d7; } } } }

.plyr__play-large {
  width: 150px;
  height: 150px;
  border: 0;
  background-color: transparent;
  background-image: url(../images/button-play.png);
  background-size: cover;
  svg {
    display: none; } }
