/* ------------------------------------------------------------------------------------------------ */
/* custom owl */
/* ------------------------------------------------------------------------------------------------ */

.owl-carousel {
  position: relative;
  overflow: hidden;
  .owl-stage-outer {
    .owl-stage {
      .owl-item {
        position: relative; } } }
  .owl-nav {
    position: absolute;
    top: calc(50% - 15px);
    width: 100%;
    .owl-prev,
    .owl-next {
      padding: 8px 0;
      width: 25px;
      background-color: $primary;
      color: #FFF;
      position: absolute;
      font-size: 14px;
      line-height: 20px;
      @include transition(all .3s);
      .fa {
        position: relative;
        top: 1px; }
      &:hover {
        background-color: $primary-dark; }
      &.disabled {
        opacity: 0;
        visibility: hidden; } }
    .owl-prev {
      left: 0;
      @include responsive(0,1000px) {
        left: -25px; } }
    .owl-next {
      right: 0;
      @include responsive(0,1000px) {
        right: -25px; } } }
  &:hover {
    .owl-nav .owl-prev {
      left: 0; }
    .owl-nav .owl-next {
      right: 0; } }
  .owl-dots {
    text-align: center;
    position: absolute;
    width: 100%;
    bottom: 0;
    line-height: 10px;
    .owl-dot {
      display: inline-block;
      margin: 0 5px;
      span {
        display: block;
        width: 10px;
        height: 10px;
        border-radius: 5px;
        background-color: #d7d7d7;
        border: 1px solid #FFF; }
      &.active {
        span {
          background-color: #636363; } } } } }
