body {
	background-image: url(../images/top-bg-big2.png), url(../images/bottom-bg.png);
	background-repeat: no-repeat, no-repeat;
	background-position: top left, bottom;
	background-size: 100% auto, 120% auto;
	@include responsive(1400px) {
		background-size: 100% auto, 110% auto; }
	@include maxtablet {
		background-size: contain, 150% auto; }
	@include mobile {
		background-image: url(../images/mobile-top-bg.png), url(../images/bottom-bg.png) !important;
		background-repeat: no-repeat, no-repeat;
		background-size: contain, 100% 17%; }
	@include responsive(375px) {
		background-size: contain, 100% 20%; } }

.sosmed {
	padding: 25px 20px;
	background-color: #0F4D70;
	border-radius: 0 5px 5px 0;
	list-style: none;
	display: inline-block;
	position: fixed;
	top: calc(50% - 150px);
	z-index: 999;
	transition: all .3s;
	&:hover {
		left: 0; }
	li {
		margin-bottom: 20px;
		line-height: 30px;
		&:last-child {
			margin-bottom: 0; }
		a {
			width: 30px;
			height: 30px;
			display: inline-block;
			color: #FFF;
			font-size: 30px; }
		&:hover {
			.fa-instagram {
				color: #823DB0; }
			.fa-twitter {
				color: #00A3EE; }
			.fa-facebook-official {
				color: #007BD3; }
			.fa-youtube-play {
				color: #E70000; } } }
	@include responsive(1440px) {
		padding: 15px 10px;
		top: calc(50% - 85px);
		li {
			margin-bottom: 15px;
			line-height: 25px;
			a {
				width: 25px;
				height: 25px;
				font-size: 25px; } } }
	@include maxtablet {
		padding: 15px 10px;
		li {
			line-height: 20px;
			margin-bottom: 10px;
			a {
				width: 20px;
				height: 20px;
				font-size: 20px; } } }
	@include mobile {
		padding: 10px 5px; } }


.navigation {
	position: fixed;
	right: 3%;
	top: calc(50% - 90px);
	z-index: 999;
	transition: all .3s;
	ul {
		li {
			list-style: none;
			margin-bottom: 25px;
			line-height: 0;
			&:last-child {
				margin-bottom: 0; }
			a {
				width: 18px;
				height: 18px;
				display: inline-block;
				background: #ddd;
				border-radius: 50%;
				position: relative;
				font-weight: 500;
				transition: all .3s;
				span {
					position: absolute;
					right: 30px;
					top: 7px;
					white-space: nowrap;
					color: #0F4D70;
					opacity: 0;
					transition: all .3s; }
				&:hover {
					background: #062E44;
					span {
						opacity: 1; } } }
			&.active {
				a {
					background: #062E44;
					span {
						opacity: 1; } } } } }
	&.nav-light {
		ul {
			li {
				&:first-child {
					a {
						span {
							color: #FFF; } } }
				a {
					span {
						color: #FFF; } }
				&:hover {
					a {
						span {
							color: #FFF; } } }
				&:last-child .active {
					a {
						span {
							color: #FFF; } } } } } }
	.burger-icon {
		display: none;
		position: absolute;
		top: 27px;
		right: 0;
		width: 35px;
		font-size: 12px;
		line-height: 12px;
		padding-top: 30px;
		transition: all 0.2s;
		z-index: 50;
		span {
			display: block;
			width: 100%;
			height: 5px;
			border-radius: 3px;
			background-color: #FFF;
			position: absolute;
			opacity: 1;
			top: 0;
			transition: all 0.2s;
			&:nth-child(2) {
				top: 10px; }
			&:nth-child(3) {
				top: 20px; } }
		&.open {
			span {
				&:nth-child(1) {
					top: 16px;
					transform: rotate(135deg); }
				&:nth-child(2) {
					opacity: 0; }
				&:nth-child(3) {
					top: 16px;
					transform: rotate(-135deg); } } } }

	@include responsive(1440px) {
		top: calc(50% - 30px);
		ul {
			li {
				margin-bottom: 15px;
				a {
					width: 10px;
					height: 10px;
					span {
						top: -3px;
						font-size: 14px;
						line-height: 1; } } } } }
	@include mobile {
		display: block;
		width: 100%;
		top: 0;
		right: 0;
		background-color: #0e547a;
		max-height: 0;
		position: fixed;
		ul {
			max-height: 0; }
		&.open {
			max-height: 999px;
			ul {
				display: block;
				transition: all .3s .2s;
				max-height: 999px;
				opacity: 1;
				li {
					span {
						color: #FFF; } } } }
		&.small {
			max-height: 0; }
		.burger-icon {
			display: block;
			top: 20px;
			right: 20px;
			&.scrolled {
				span {
					background: #24799b; } } }
		ul {
			padding: 20px;
			padding-top: 60px;
			max-height: 0;
			opacity: 0;
			display: none;
			transition: all .3s .3s;
			li {
				width: 100%;
				margin-bottom: 10px;
				a {
					border-radius: 0;
					display: block;
					width: 100%;
					height: auto;
					background: transparent;
					opacity: 1;
					span {
						text-align: right;
						padding: 5px 10px;
						display: block;
						opacity: 1;
						position: relative;
						left: auto;
						right: auto;
						top: auto;
						bottom: auto; } } } } } }

@keyframes wave {
	0% {
		transform: translateX(0) translateY(0); }
	100% {
		transform: translateX(0) translateY(35px); } }

